import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import {IntializeVuetify} from './vuetifySettings'
Vue.config.productionTip = false;

IntializeVuetify();

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
