export namespace R3 {
    export function postData(url: string, dto: any, lang : string='en'): Promise<any> {
        return fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept-Language' : lang,
            },
            body: JSON.stringify(dto),
        }).then((r) => {
            if (r.ok) {
                return r.json();
            }

            r.json().then((r: any) => {
                // TODO: Implement error signal to UI.
                console.log('Error');
                console.log(JSON.stringify(r, null, 2));
            });

            throw new Error(`Error in POST to ${url}.`);
        });
    }

}
